<template>
  <div>
    <searchnav title="电影页面"></searchnav>
    <!-- <h2>电影</h2> -->
    <van-tabs v-model="active">
      <van-tab title="歌单列表">
        <van-card :desc="item.name"
                  :title="item.copywriter"
                  :thumb="item.coverImgUrl"
                  v-for="(item, index) in mList"
                  :key="index">
          <template #num>
            <van-icon name="share"
                      color='orange' />
            <van-tag plain
                     type="warning">{{item.shareCount}}</van-tag>

          </template>
          <template #price>
            <van-icon name="fire-o"
                      color='red' />
            <van-tag plain
                     type="danger">{{item.playCount}}</van-tag>

          </template>
          <template #tags>
            <van-tag plain
                     :type="type[i]"
                     v-for="(cell, i) in item.tags"
                     :key="i">{{cell}}</van-tag>

          </template>
          <template #footer>
            <van-button size="mini"
                        @click='listen()'>试听</van-button>
            <van-button size="mini">收藏</van-button>
          </template>
        </van-card>
      </van-tab>
      <van-tab title="正在播放">
        <van-card num="2"
                  price="2.00"
                  desc="描述信息"
                  title="商品标题"
                  thumb="https://img.yzcdn.cn/vant/ipad.jpeg">
          <template #tags>
            <audio :src="play"
                   autoplay
                   controls></audio>
            <van-tag plain
                     type="danger">标签</van-tag>
            <van-tag plain
                     type="danger">标签</van-tag>
          </template>
          <template #footer>
            <van-button size="mini">按钮</van-button>
            <van-button size="mini">按钮</van-button>
          </template>
        </van-card>
      </van-tab>

    </van-tabs>

  </div>
</template>
<script>
export default {
  data () {
    return {
      active: 0,
      mList: [],
      type: ['primary', 'success', 'danger ', 'warning'],
      play: '',
      mv: ''
    }
  },
  methods: {
    listen () {

    }
  },
  mounted () {

    this.axios.get('/yun/top/playlist/highquality?limit=30').then(res => {
      console.log(res)
      this.mList = res.data.playlists
    })
  },
  updated () {
    if (this.active === 1) {
      this.axios.get('/yun/song/url?id=33894312').then(res => {
        console.log(res)

        this.play = res.data.data[0].url
      })

    }
  },



}
new Date().toLocaleString
</script>
<style lang="scss">
.link {
  display: flex;
  width: 100%;
  height: 40px;
  font-size: 20px;
}
a {
  flex: 1;
  text-align: center;
  line-height: 40px;
}
.router-link-active {
  color: green;
  background: bisque;
}
</style>

